import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { OppsComponent } from './opps.component';
import { ApprenticesComponent } from 'app/opps/apprentices/apprentices.component';
import { MentorsComponent } from 'app/opps/mentors/mentors.component';
import { BusinessesComponent } from 'app/opps/businesses/businesses.component';

const routes: Routes = [
  { path: '', component: OppsComponent },
  { path: 'apprentices', component: ApprenticesComponent },
  { path: 'entrepreneurs', component: MentorsComponent },
  { path: 'enterprises', component: BusinessesComponent }
];

export const routing: ModuleWithProviders = RouterModule.forChild(routes);


